#navBar {
  padding: 16px;
  text-align: center;
  background-color: white; }
  #navBar .logo {
    background: url("/images/logo.png") no-repeat center center;
    background-size: contain;
    height: 128px;
    width: 128px;
    display: inline-block;
    margin-top: 32px;
    vertical-align: bottom; }
  #navBar .links {
    position: absolute;
    bottom: 32px;
    left: 32px;
    text-align: left; }
  #navBar a {
    color: #E63F57;
    font-weight: 500;
    text-transform: uppercase;
    display: block;
    margin: 8px 0; }
    #navBar a:hover {
      color: #d61c37; }



