#pageLayout {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #F8F8F8;
  overflow: auto; }
  #pageLayout #content,
  #pageLayout #navBar {
    display: inline-block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }
  #pageLayout #navBar {
    width: 240px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0; }
  #pageLayout #content {
    margin-left: 240px;
    width: calc(100% - 240px);
    padding: 16px; }



